.body-header{
    font-size: 45px;
    text-align: center;
    color: #5C2E76;
    font-weight: bold;
    margin-top: 25px;
}
.body-subheader{
    text-align: center;
    font-size: 22px;
    color: #F45A96 ;
    
}
.body-subheaderdot{
    color:  #5C2E76;
}
.bodycard{
    box-shadow: 2px 3px 20px 1px #efefef;
    margin-bottom: 24px;
}
.bodycard-container{
    padding: 10px 30px
}
.bodycard-title{
    text-align: center;
    font-size: 25px;
    color: #5C2E76;
    font-weight: bold;
}
.card-button{
   font-size: 18px;
   background-color:#E83E80; ;
   color: white;
   border-style: none;
   height: 40px;
}
.lable-style{
    color: #F45A96 ;
    margin-left: 5px;
}
.check-question{
    font-size: 22px;
    margin-bottom: 1px;
    color: #5C2E76;
}

.cardinput-containerstyle{
   height: 47px;
   border: 1px solid #F45A96  ;
   padding-left: 50px;
   overflow: hidden;
}
.cardinput-style{
    height: 100%;
    border: none;

}
.cardinput-style:focus{
    outline: none;
}
.cardselect-containerstyle{
    height: 47px;
}
.select-input{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 3px solid rgb(194, 193, 193);
    padding: 20px;
    color: rgb(194, 193, 193);
  
  
}

.slect-logo{
    width: 21px;
    height: 11px;
    fill: #F45A96 ;
}
.treatment-header{
    font-size: 30px;
    color: #5C2E76;
    text-align: center;
    margin-bottom: 25px;
}
.treatment-img{
    
    height: 147px;
}
.treatment-name{
    font-size: 23px;
    color: #5C2E76;
    text-align: center;
    margin-top: 10px;

}
.service-img{
    width: 129px;
    height: 129px;
}
.service-name{
    font-size: 23px;
    text-align: center;
    line-height: 1.2em;
    font-weight: bold;
    color: #5C2E76;
}
.service-desc{
    font-size: 18px;
    text-align: center;
     color: #8b8686;
     width: 250px;
     
}
.service-btn{
    width: 142px;
    height: 40px;
    border: none;
    color: white;
    background-color: #E83E80;
}
.service-title{
    font-size: 30px;
    color: #5C2E76;
    text-align: center;
    font-weight: bold;
    margin-bottom: 20px;
}
.left-navbar{
    position: fixed;
    z-index: 1;
    height: 100vh;
     background-color: white;
     width: 70%;
     animation-name: fadeInRight;
     animation-duration: 1s;
}
.left-navbarContainer{
    padding: 30px 3px;

}
.left-navlistContainer{
    list-style: none;
    margin-left: 15px;
    padding-left: 0px;
}
.nav-item{
    margin-bottom: 20px;
    color: #5C2E76;
    font-size: 20px;
 
}
@keyframes fadeInRight {
    0% {
       opacity: 0;
       transform: translateX(50px);
    }
    100% {
       opacity: 1;
       transform: translateX(0);
    }
}
