.header-container{
    padding-left:10% ;
    padding-right: 10%;
    display: block;
}
.header-top{
    display: flex;
    justify-content: flex-end;
    position: sticky;
}
.header-bottom{
    padding: 10px;
    display: flex;
    align-items: center;
   
    
}
.header-topContainer{
    
    margin-right: 300px;
    display: flex;
    align-items: center;
    padding-top: 10px ;
    

}
.header-bottomLeft{
    flex: 1;
}
.header-bottomRight{
    flex: 1;
   
   

  

}
.header-topitem{
     color: #F45A96 ;
}
.logo{
    font-size: 34px;
     font-weight: bold;
}
.logo1{
    color: #5C2E76;
}
.logo2{
    color: #F45A96;

}
.items-container{
    display: flex;
    justify-content: flex-end;
    margin-bottom: 0px;
    
}
.whatsuplogo{
    width: 28px;
    height: 28px;
    margin-left: 20px;
}
.item{
    display: flex;
    justify-content: center;
    align-items: center;
    color:#5C2E76 ;
    line-height: 28px;
   
    font-weight: bold;
    width: 138px;
    font-size: 15px;
    margin-bottom: 0px;
    text-align: left;
}
    

.collpase-headerContainer{
    padding: 40px 20px;
    display: flex;
    align-items: center;

}
.collpaseheader-Left{
    flex: 1;


}
.collpaseheaderLogo{
    margin-bottom: 0px;
    font-size: 28px;
    font-weight: bold;
}
.logo{
    margin-bottom: 0px;
}
.headerright-text{
    font-size: 18px;
    font-weight: 600;
    color: #F45A96;
    text-align: center;
    margin-bottom: 0px;

}
.collpase-header{
    display: none;
}

@media(max-width:900px){
    .header-container{
        display: none;
    }
    .collpase-header{
        display: block;
    }
}